@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:wght@400;600&display=swap");
* {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background-image: url("../public/images/bg-desktop.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-color: #674baf;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  justify-items: space-between;
}

#header {
  width: 100%;
  padding-top: 3%;
}

#header .logo-container {
  padding-left: 5%;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

#hero {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 90%;
  margin-left: 5%;
  margin-top: 5%;
}

#hero .h-right {
  margin-left: 5%;
}

#hero h1 {
  padding-top: 5%;
  font-size: 3em;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: white;
  width: 80%;
}

#hero p {
  font-family: "Open Sans", sans-serif;
  font-size: 1.2em;
  width: 80%;
  line-height: 1.6;
  padding-top: 4%;
  color: white;
}

#hero #register {
  padding: 2% 6%;
  font-size: 1.2em;
  border: none;
  border-radius: 40px;
  letter-spacing: 1px;
  margin-top: 5%;
  color: #674baf;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  -webkit-box-shadow: 0 2px 10px 2px #3f3d3d;
          box-shadow: 0 2px 10px 2px #3f3d3d;
  cursor: pointer;
  -webkit-transition: all 250ms ease-in;
  transition: all 250ms ease-in;
}

#hero #register:hover {
  background-color: #e882e8;
  color: white;
}

#footer {
  width: 85%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  margin-right: 15%;
  margin-top: 5%;
}

#footer .icons-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 200px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  cursor: pointer;
}

#footer .ionicon {
  width: 40px;
  fill: white;
  -webkit-transition: fill 200ms ease-in-out;
  transition: fill 200ms ease-in-out;
  padding: 5%;
}

#footer #twitter:hover {
  fill: #e882e8;
}

#footer .ionicon:hover {
  fill: #e882e8;
}

@media only screen and (max-width: 600px) {
  body {
    background-image: url("../public/images/bg-mobile.svg");
    background-size: contain;
  }
  #header #logo {
    margin-bottom: 10%;
  }
  #hero {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100% !important;
  }
  #hero .h-left {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin-bottom: 8%;
  }
  #hero #illustration {
    width: 90vw;
  }
  #hero .h-right {
    width: 80%;
    margin-left: 8%;
    text-align: center;
    margin-bottom: 5%;
  }
  #hero h1 {
    width: 100% !important;
    font-size: 2em;
    margin-bottom: 6%;
  }
  #hero p {
    font-size: 1em;
    text-align: center;
    width: 100%;
    margin-bottom: 8%;
  }
  #hero #register {
    padding: 2% 25%;
  }
  #footer {
    margin-right: 0 !important;
    width: 90%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin-left: 8%;
  }
}

@media only screen and (max-width: 1400px) and (min-width: 1000px) {
  #hero #illustration {
    width: 600px;
  }
  #hero .h-right {
    width: 100%;
    margin-right: 0 !important;
  }
  #hero h1 {
    margin-right: 0 !important;
    font-size: 2em;
  }
  #hero p {
    font-size: 1em;
  }
  #footer {
    margin-right: 0 !important;
    width: 80%;
    margin-left: 10%;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
  }
}
