@import './imports';
*{
    margin: 0;
    padding: 0;
}
body{
    height: 100vh;
    background-image: url('../public/images/bg-desktop.svg');
    background-repeat: no-repeat;
    background-size: contain;
    background-color: $violet;
    display: flex;
    flex-direction: column;
    justify-items: space-between;
}
#header{
    width: 100%;
    padding-top: 3%;
    .logo-container{
        padding-left : 5%;
        width: max-content; 
    }
}
#hero{
    display: flex;
    width: 90%;
    margin-left: 5%;
    margin-top: 5%;
    .h-right{
        margin-left: 5%;
    }
    h1{
        padding-top: 5%;
        font-size: 3em;
        font-family: $f-poppins;
        font-weight: 600;
        color: white;
        width: 80%;
    }
    p{
        font-family: $f-open-sans;
        font-size: 1.2em;
        width: 80%;
        line-height: 1.6;
        padding-top: 4%;
        color: lighten($s-magenta,30);
    }
    #register{
        padding: 2% 6%;
        font-size: 1.2em;
        border: none;
        border-radius: 40px;
        letter-spacing: 1px;
        margin-top: 5%;
        color: $violet;
        font-family: $f-poppins;
        font-weight: 600;
        box-shadow: 0 2px 10px 2px rgb(63, 61, 61);
        cursor: pointer;
        transition: all 250ms ease-in;
    }
    #register:hover{
        background-color: $s-magenta;
        color: white;
    }
}
#footer{
    width: 85%;
    display: flex;
    justify-content: flex-end;
    margin-right: 15%;
    margin-top: 5%;
    .icons-container{
        display: flex;
        width: 200px;
        justify-content: space-between;
        cursor: pointer;
    }
    .ionicon{
        width: 40px;
        fill: white;
        transition: fill 200ms ease-in-out;
        padding: 5%;
    }
    #twitter:hover{
        fill: $s-magenta;
    }
    .ionicon:hover{
       fill: $s-magenta;
    }
}

@media only screen and (max-width: 600px) {
    body{
        background-image: url('../public/images/bg-mobile.svg');
        background-size: contain;
    }
    #header{
        #logo{
            margin-bottom: 10% ;
        }
    }
    #hero{
        flex-direction: column;
        width: 100% !important;
        .h-left{
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 8%;
        }
        #illustration{
            width: 90vw;
        }
        .h-right{
            width: 80%;
            margin-left: 8%;
            text-align: center;
            margin-bottom: 5%;
        }
        h1{
            width: 100% !important;
            font-size: 2em;
            margin-bottom: 6%;
        }
        p{
            font-size: 1em;
            text-align: center;
            width: 100%;
            margin-bottom: 8%;
        }
        #register{
            padding: 2% 25%;
        }
    }
    #footer{
        margin-right: 0 !important;
        width: 90%;
        justify-content: center;
        margin-left: 8%;
    }

    
}
@media only screen and (max-width: 1400px) and (min-width: 1000px){
    #hero{
        #illustration{
            width: 600px;
        }
        .h-right{
            width: 100% ;
            margin-right: 0 !important;
        }
        h1{
            margin-right: 0 !important;
            font-size: 2em;
        }
        p{
            font-size: 1em;
        }
    }
    #footer{
        margin-right: 0 !important;
        width: 80%;
        margin-left: 10%;
        justify-content: flex-end;
        //margin-right: 6% !important;
    }
}
